import React, { Component } from 'react';
export default class Header extends Component {
  render() {
	  let resumeData=this.props.resumeData;
    return (
      <React.Fragment>


       <header id="home">
        <nav id="nav-wrap">
          <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
          <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
          <ul id="nav" className="nav">
            <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
            <li><a className="smoothscroll" href="#experience">Experience</a></li>
            <li><a className="smoothscroll" href="#projects">Projects</a></li>
            <li><a className="smoothscroll" href="#about">About</a></li>
          </ul> {/* end #nav */}
        </nav> {/* end #nav-wrap */}


        <div className="row banner">

          <div className="banner-text">

            <h1 className="responsive-headline">{resumeData.name}.</h1>
            <h3>Hello, I am Jimmy!
           <br/> A Computer Science major at JHU, I love Software Engineering and research.<br/>I especially love optimization, designing scalable systems and dreaming about future technologies!
            </h3>
            <hr />

          </div>
        </div>
        <p className="scrolldown">
          <a className="smoothscroll" href="#about"><i className="icon-down-circle" /></a>
        </p>
      </header>
      </React.Fragment>
    );
  }
}